<template>
  <select :value="value" @change="onChange($event)">
    <option value="">No filter</option>
    <option v-for="(genre, index) in genreList" :key="index">{{ genre }}</option>
  </select>
</template>

<script>
export default {
  props: {
    value: String,
    genreList: Array,
  },
  methods: {
    onChange(event) {
      this.$emit("selected", event.target.value);
    },
  }
};

</script>

<style scoped>
  select {
    color: white;
    background: rgb(18, 18, 18);
    padding: 4px 10px;
    border-radius: 6px;
    border: 1px solid white;
  }
</style>