<template>
  <header>
    <Filter :genreList="genreList" @selected="filtering($event)"/>
  </header>

  <main>
    <GameList :gameList="gameList" :filter="filter"/>
  </main>

  <footer>
    <p>API provided by FreeToGame.com</p>
  </footer>
</template>

<script>
import GameList from './components/GameList.vue'
import Filter from './components/Filter.vue'
import axios from "axios"

export default {
  name: 'App',
  components: {
    GameList,
    Filter,
  },
  data() {
    return {
      gameList: [],
      genreList: [],
      filter: "",
    };
  },
  emits: ["selected"],
  methods: {
    filtering(choice) {
      this.filter = choice;
    },
  },
  
  async created() {
    const options = {
      method: 'GET',
      url: 'https://free-to-play-games-database.p.rapidapi.com/api/games',
      params: {platform: 'pc'},
      headers: {
        'x-rapidapi-host': 'free-to-play-games-database.p.rapidapi.com',
        'x-rapidapi-key': 'c31220b699mshbd63a901c6d4d79p160bbfjsndccc66cf1900'
      }
    };

    axios.request(options).then(response => {
      console.log(response.data);
      this.gameList = response.data;
      for (let i = 0; i < this.gameList.length; i++) {
        if (this.genreList.includes(this.gameList[i].genre) == false) {
          this.genreList.push(this.gameList[i].genre);
        }
      }
      this.genreList = this.genreList.sort();
    }).catch(function (error) {
      console.error(error);
    });
  }
}
</script>

